<template>
<header class="header" :class="{ 'open': isOpen }">
  <div class="container">
     <div class="header_body">
       <a href="#" class="header_logo">
         <img src="../assets/logo.png" alt="Logo">
       </a>
       <div class="header_burger" @click="burgerClick" :class="{ 'open': isOpen }">
         <span></span>
       </div>
       <nav class="header_menu" :class="{ 'open': isOpen }">
         <ul class="header_list">
           <li @click.prevent="scrollPage('#AppMain', 600)">
             <a href="#" class="header_link" >
               {{ $t('navbar.home') }}
             </a>
           </li>
           <li @click.prevent="scrollPage('#AppAboutUs', 600)">
             <a href="#" class="header_link">
               {{ $t('navbar.about_us') }}
             </a>
           </li>
           <li @click.prevent="scrollPage('#AppQuestions', 600)">
             <a href="#" class="header_link">
               {{ $t('navbar.questions') }}
             </a>
           </li>
           <li @click.prevent="scrollPage('#AppFeatures', 600)">
             <a href="#" class="header_link">
               {{ $t('navbar.features') }}
             </a>
           </li>
           <li @click.prevent="scrollPage('#AppJoinUs', 600)">
             <a href="#" class="header_link contact_button">
               {{ $t('navbar.contact_us') }}
             </a>
           </li>
         </ul>
       </nav>
     </div>
  </div>
</header>
</template>

<script>
export default {
  name: 'AppNav',
  data: () => ({
    isOpen: false,
  }),
  methods: {
    burgerClick() {
      this.isOpen = !this.isOpen;
      if (document.body.classList.contains('overflow-hidden')) {
        document.body.classList.remove('overflow-hidden');
      } else {
        document.body.classList.add('overflow-hidden');
      }
    },
    scrollPage(component, time) {
      this.isOpen = !this.isOpen;
      if (document.body.classList.contains('overflow-hidden')) {
        document.body.classList.remove('overflow-hidden');
      }
      this.$emit('nav_click', component, time);
    },
  },
};
</script>

