<template>
  <div id="app">
    <div class="change-lang-wrap">
      <div class="b-wrap">
        <span
          class="change-lang-button"
          @click.prevent="isActiveLang = !isActiveLang"
        >
          {{ $i18n.locale }}
        </span>
        <span
          class="change-lang-button-2"
          :class="{ 'active': isActiveLang }"
          @click.prevent="changeLang(secondLang)"
        >
          {{ secondLang }}
        </span>
      </div>
    </div>
    <div class="wrap">
      <AppNav v-on:nav_click="scrollToComponent"></AppNav>
      <AppMain id="AppMain" v-on:nav_click="scrollToComponent"></AppMain>
      <AppAboutUs id="AppAboutUs"></AppAboutUs>
      <AppVideo></AppVideo>
      <AppQuestions id="AppQuestions"></AppQuestions>
      <AppFeatures id="AppFeatures"></AppFeatures>
      <AppJoinUs id="AppJoinUs"></AppJoinUs>
      <AppFooter></AppFooter>
    </div>
  </div>
</template>

<script>
// plugins
import VueScrollTo from 'vue-scrollto';

// components
import AppMain from './components/AppMain.vue';
import AppNav from './components/AppNav.vue';
import AppAboutUs from './components/AppAboutUs.vue';
import AppVideo from './components/AppVideo.vue';
import AppQuestions from './components/AppQuestions.vue';
import AppFeatures from './components/AppFeatures.vue';
import AppJoinUs from './components/AppJoinUs.vue';
import AppFooter from './components/AppFooter.vue';

export default {
  name: 'App',
  components: {
    AppMain,
    AppNav,
    AppAboutUs,
    AppVideo,
    AppQuestions,
    AppFeatures,
    AppJoinUs,
    AppFooter,
  },
  data: () => ({
    isLock: false,
    isActiveLang: false,
  }),
  methods: {
    scrollToComponent(component, time) {
      console.log(component || '  ');
      VueScrollTo.scrollTo(component, time);
    },
    // eslint-disable-next-line consistent-return
    changeLang(value) {
      if (this.$i18n.locale === value) {
        return false;
      }
      this.$i18n.locale = value;
      this.isActiveLang = !this.isActiveLang;
    },
  },
  computed: {
    secondLang() {
      return this.$i18n.locale === 'ru' ? 'en' : 'ru';
    },
  },
};
</script>

